import React, { useState, useEffect } from "react";
import { getAccount, getImage, mint } from "./MintUtility.js";
import "./Mint.css";
import "../App.css";
import Web3 from "web3";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Inventory from "../Inventory.js";

const ABIS = require("../ABI/Abis.json");

export default function Mint() {
  const [web3State, setWeb3State] = useState(undefined);
  const [account, setAccount] = useState(undefined);
  const [TMAContract, setTMAContract] = useState(undefined);
  const [NFTImages, setNFTImages] = useState([]);
  const [publicMintCount, setPublicMintCount] = useState(70);

  useEffect(() => {
    async function initWeb3() {
      let _web3 = new Web3(window.ethereum);
      await window.ethereum.enable();

      let NFTContract = new _web3.eth.Contract(ABIS["TheModernAcreNFTPass"].ABI, ABIS["TheModernAcreNFTPass"].Address);
      let _totalMintCount = await NFTContract.methods.amountMintedTotal().call();
      let _publicMintCount = await NFTContract.methods.amountMintedPublic().call();
      _totalMintCount < 70
        ? setPublicMintCount(70 + parseInt(_publicMintCount))
        : setPublicMintCount(parseInt(_totalMintCount));

      setWeb3State(_web3);
      setAccount(await getAccount(_web3));
      setTMAContract(NFTContract);
      await getImage(_web3, setNFTImages, NFTContract);
    }

    initWeb3();
  }, []);

  useEffect(() => {
    console.log(NFTImages);
  }, [NFTImages]);

  return (
    <>
      <Inventory style={{ height: "40vh" }}></Inventory>
      <div
        className="infogram"
        style={{
          border: "5px black solid",
          float: "left",
          width: "50vw",
          height: "40vh",
          margin: "0",
          marginLeft: "1.5vw",
          marginTop: "5vh",
          display: "inline-block",
          verticalAlign: "top",
        }}
      >
        <p>
          Get started by joining the Telegram chat:{" "}
          <a style={{ color: "#5bc09a" }} target="_blank" href="https://themodernacre.com/telegram">
            https://themodernacre.com/telegram
          </a>
        </p>
        <p>
          *If joining on mobile, choose the "Wallet Connect" option when prompted by Collab.Land* Join the telegram now
          here
        </p>
        <p>
          View the collection{" "}
          <a style={{ color: "#5bc09a" }} target="_blank" href="https://opensea.io/collection/themodernacre">
            here
          </a>
        </p>
        {/* <p style={{
        width: "100%",
          View your NFT #{} <a style={{color: "#5bc09a"}} href={`https://opensea.io/assets/0xe0e0ac998a2ae2550d0db6c819bf2f9a4ce1caf5/${}`}>here</a>
        </p> */}

        <p>Tell us more about yourself so we can stay connected: https://themodernacre.com/memberform</p>
        <p>
          Reach out to us with any concerns, questions, or suggestions at{" "}
          <a style={{ color: "#5bc09a" }} target="_blank" href="mailto:tyler@themodernacre.co">
            tyler@themodernacre.co
          </a>
          . Otherwise, we'll see you in Telegram!
        </p>
      </div>
      <div className="mintWrapper">
        <img className="tmaPicture" src="TMACo-op.gif" alt="The Modern Acre" />
        <label style={{ color: "white" }}>{publicMintCount}/210</label>
        <button className="appButton mintButton" onClick={() => mint(web3State, setNFTImages, TMAContract)}>
          Mint
        </button>
      </div>
      <ToastContainer />
    </>
  );
}
